import axios from "axios";
import { createContext, useCallback, useEffect, useState } from "react";

export let Apicontext = createContext("");
function ApicontextProvider(props) {
  //------------------------- states      ------------------
  const [terdMovie, setterdMovie] = useState([]);
  const [allPostes, setAllPostes] = useState([]);

  useEffect(() => {
    console.log(1);
  });

  //---------------------------------componat will mount onchange----------------------------

  // --------------------------------- function to get all media type from api      ----------------------------

  const fetchData = useCallback(async (id) => {
    let { data } = await axios.get(
      `https://laravel.hekaia.net/api/get-story?story_id=${id}`
    );
    setterdMovie(data.data[0].sliders);
    //   settest(data.data[0].sliders[0].media)
    console.log(data.data[0].sliders);
    console.log(data.data[0].sliders[0].media);
  }, []);

  const fetchStories = useCallback(async () => {
    let { data } = await axios.get(
      "https://laravel.hekaia.net/api/get-all-stories"
    );
    setAllPostes(data.data);
    //   settest(data.data[0].sliders[0].media)
    // console.log(data.data[0].sliders);
    // console.log(data.data[0].sliders[0].media);
  }, []);

  useEffect(() => {
    // fetchData();
    fetchStories();
  }, [fetchStories]);
  return (
    <Apicontext.Provider value={{ terdMovie, fetchData, allPostes }}>
      {props.children}
    </Apicontext.Provider>
  );
}
export default ApicontextProvider;

